export default {
	props: {
		// nullDayNum: {
		// 	type: Array,
		// 	default: () => []
		// },
		days: {
			type: Array,
			default: () => []
		},
		// //高度
		// theHight: {
		// 	type: String,
		// 	default: '400rpx'
		// },
		show: {
			type: Boolean,
			default: false
		},
		showIndex: {
			type: Number,
			default: 0
		},
		theDownTimeChoseShow: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			weeks: ['周一',
				'周二',
				'周三',
				'周四',
				'周五',
				'周六',
				'周末'
			],
			day: '',
			week: '',
			theHight: '',
			nullDayNum: [],
			
		}
	},
	methods: {
		changeTimeChangeShow: function() {
			this.$emit('showChange', !this.show)
		},
		changeIndex: function(index) {
			this.$emit('showIndexChange', index)
		},
		changeDate: function(){
			let date = new Date()
			date.setDate(date.getDate()+this.showIndex)
			this.day = date.getFullYear()+'-'+(date.getMonth()+1)+'-'+date.getDate()
			switch(date.getDay()){
				case 0:
					this.week = '星期日'
					break
				case 1:
					this.week = '星期一'
					break
				case 2:
					this.week = '星期二'
					break
				case 3:
					this.week = '星期三'
					break
				case 4:
					this.week = '星期四'
					break
				case 5:
					this.week = '星期五'
					break
				case 6:
					this.week = '星期六'
					break
				default:
					break
			}
		}
	},			
	watch:{
		showIndex: function() {
			this.changeDate()
		}
	},
	created(){
		this.changeDate()
		this.theHight = (Math.floor((this.days.length + this.nullDayNum.length) / 7) + 1) * 84 + 84 + 10 + 'rpx';
		let time = new Date()
		let nowTime = time.getDay()
		if (nowTime == 0) {
			nowTime = 7
		}
		this.nullDayNum = []
		for (let i = 0; i < nowTime - 1; i++) {
			this.nullDayNum.push(i)
		}
	}
}
